body {
  background-color: #ecedff;
  font-family: "inter-regular", sans-serif;
}

.container {
  max-width: 1440px;
  padding: 0 64px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #222531;
  margin: 0;
}

.checkbox-wrapper {
  margin: 0 16px;
}
@media (max-width: 1399.98px) {
  .checkbox-wrapper {
    margin: 0 10px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper {
    margin: 0 5px;
  }
}

.checkbox-wrapper input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.checkbox-wrapper label {
  display: flex;
  align-items: center;
  position: relative;
  margin: 0;
  cursor: pointer;
  font-size: 12px;
}
@media (max-width: 991.98px) {
  .checkbox-wrapper label {
    font-size: 16px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper label {
    font-size: 14px;
  }
}
@media (max-width: 390px) {
  .checkbox-wrapper label {
    flex-direction: column-reverse;
  }
}

.checkbox-wrapper label:before {
  content: "";
  background-color: transparent;
  border: 2px solid;
  border-color: #eff2f5;
  padding: 7px;
  position: relative;
  cursor: pointer;
  margin-right: 5px;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}
@media (max-width: 1199.98px) {
  .checkbox-wrapper label:before {
    padding: 5px;
  }
}
@media (max-width: 991.98px) {
  .checkbox-wrapper label:before {
    padding: 7px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper label:before {
    padding: 5px;
  }
}

.checkbox-wrapper input:checked + label::before {
  background-color: #eff2f5;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 64px 64px 32px 64px;
}
@media (max-width: 991.98px) {
  .header__inner {
    flex-direction: column;
    padding: 35px 25px 15px 25px;
  }
}
@media (max-width: 390px) {
  .header__inner {
    padding: 25px 15px 30px 15px;
  }
}

.intro-wrapper {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
@media (max-width: 991.98px) {
  .intro-wrapper {
    align-items: center;
    margin-bottom: 20px;
  }
}
.intro-wrapper h1 {
  line-height: 2rem;
  font-size: 2em;
}
.intro-wrapper p {
  color: #616e85;
  line-height: 1.5em;
  font-size: 1em;
  margin: 0;
}
@media (max-width: 575.98px) {
  .intro-wrapper p {
    font-size: 0.9em;
  }
}

.navigation {
  width: 100%;
  max-width: 418px;
  border-radius: 72px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  max-height: 56px;
}
@media (max-width: 575.98px) {
  .navigation {
    max-width: 322px;
  }
}
@media (max-width: 336px) {
  .navigation {
    padding: 8px 0;
  }
}
.navigation ul {
  display: inline-flex;
  height: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
  border-radius: 72px;
}
@media (max-width: 336px) {
  .navigation ul {
    width: 100%;
    justify-content: space-between;
  }
}
.navigation a {
  font-size: 14px;
  display: flex;
  align-items: flex-start;
  height: 100%;
  border-radius: 40px;
  padding: 12px 24px;
  gap: 5px;
  box-sizing: border-box;
  color: #222531;
  font-family: "inter-semi-bold", sans-serif;
  text-decoration: none;
  transition: all 0.5s ease-out;
}
@media (max-width: 575.98px) {
  .navigation a {
    padding: 5px 7px;
  }
}
@media (max-width: 336px) {
  .navigation a {
    font-size: 11px;
    padding: 5px 4px;
  }
}
.navigation a:hover {
  background-color: #4c57fb;
  color: #fff;
  transition: all 0.5s ease-out;
}
.navigation a:hover i {
  color: #fff;
}
.navigation i {
  font-size: 16px;
  color: #4c57fb;
  transition: all 0.5s ease-out;
}

.dashboard__inner {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2em;
  padding: 0 64px 23px 64px;
}
@media (max-width: 991.98px) {
  .dashboard__inner {
    grid-template-columns: repeat(2, 1fr);
    padding: 25px 25px 30px 25px;
  }
}
@media (max-width: 390px) {
  .dashboard__inner {
    gap: 1em;
    padding: 25px 15px 30px 15px;
    grid-template-columns: repeat(1, 1fr);
  }
}

.card {
  display: flex;
  flex-direction: column;
  height: 208px;
  border-radius: 1em;
  background: #fff;
  overflow: hidden;
  box-shadow: 0px 8px 24px 0px rgba(81, 85, 195, 0.08);
  position: relative;
}
@media (max-width: 390px) {
  .card {
    height: 100%;
  }
}
.card:last-child, .card:nth-last-child(2) {
  grid-column: span 2;
}
@media (max-width: 390px) {
  .card:last-child, .card:nth-last-child(2) {
    grid-column: auto;
  }
}
.card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 24px 16px 24px;
}
.card__content {
  height: 100%;
}
.card__chart-figure-wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 132px;
  height: 132px;
}
.card__chart-figure-wrapper .chart-figure {
  width: 100%;
  height: auto;
}
.card__percent-value-wrapper {
  display: flex;
  justify-content: space-between;
}
.card__percent-value-wrapper span {
  width: 20px;
  font-size: 10px;
  color: #616e85;
  line-height: 1em;
}
.card__percent-value-wrapper span:first-child {
  margin-left: 10px;
}
.card__percent-value-wrapper span:last-child {
  margin-right: 5px;
}
.card__footer {
  display: flex;
  align-items: baseline;
  padding: 12px 22px 15px 24px;
  border-radius: 0px 0px 16px 16px;
  position: relative;
  z-index: 0;
}
.card__footer--type1 {
  color: #fff;
  background-color: #f18c5c;
  justify-content: space-between;
}
.card__footer--type1::before {
  content: url("../images/wave.svg");
  position: absolute;
  z-index: 1;
  top: -24px;
  left: 0;
  width: 100%;
  height: auto;
}
@media (max-width: 991.98px) {
  .card__footer--type1::before {
    top: -25px;
    left: 72px;
    transform: scale(2);
    z-index: -1;
  }
}
@media (max-width: 461px) {
  .card__footer--type1::before {
    top: -2px;
  }
}
.card__footer--type1 .big-value {
  font-size: 24px;
  line-height: 24px;
  font-family: "inter-bold", sans-serif;
}
@media (max-width: 1199.98px) {
  .card__footer--type1 .big-value {
    font-size: 19px;
    line-height: 19px;
  }
}
.card__footer--type1 .small-value {
  font-size: 14px;
}
@media (max-width: 461px) {
  .card__footer--type1 {
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 390px) {
  .card__footer--type1 {
    flex-direction: row;
  }
}
.card__footer--type2 {
  width: 100%;
  position: absolute;
  bottom: 3px;
  justify-content: center;
  padding: 0 22px 15px 24px;
}
.card__footer--type2 .form-wrapper {
