// Icomoon variables path
@import "../base/icomoon-variables";

@font-face {
  font-family: "#{$icomoon-font-family}";
  src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?w1mld4");
  src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?w1mld4#iefix")
      format("embedded-opentype"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?w1mld4")
      format("truetype"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.woff?w1mld4")
      format("woff"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.svg?w1mld4##{$icomoon-font-family}")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "#{$icomoon-font-family}" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-bag-icon {
  &:before {
    content: $icon-bag-icon;
  }
}
.icon-chart-icon {
  &:before {
    content: $icon-chart-icon;
  }
}
.icon-info-icon {
  &:before {
    content: $icon-info-icon;
  }
}
.icon-people-icon {
  &:before {
    content: $icon-people-icon;
  }
}
